<template>
  <li v-if="item.name !== 'id'">
    <span>
      <p style="text-align: left">
        <strong>{{
          item.name !== undefined
            ? capitalizeFirst(item.name) + ":"
            : "–ò–º—è –Ω–µ –∑–∞–¥–∞–Ω–æ"
        }}</strong>
      </p>
      <p v-if="!isEditing">
        {{ item.value !== undefined ? item.value : "–ù–æ–º–µ—Ä –Ω–µ –∑–∞–¥–∞–Ω" }}
      </p>

      <div class="change_field" v-else>
        <button class="edit-btn" @click="accept–°hanges()">&#10004;</button>
        <input type="text" placeholder="New value..." v-model="value" />
        <button class="edit-btn" @click="showModal()">&times;</button>
      </div>
    </span>
    <div class="item-btns">
      <button @click="editBtn()">&#9998;</button>
      <button
        @click="
          $emit('show-modal', {
            id: item.id,
            title: '–£–¥–∞–ª–∏—Ç—å ' + item.name + '?',
            name: item.name,
            type: 'delete',
          })
        "
      >
        &times;
      </button>
    </div>
  </li>
</template>

<script>
export default {
  data() {
    return {
      value: "",
      isEditing: false,
    };
  },
  props: {
    item: {
      type: Object,
      required: true,
    },
    type: {
      type: String,
    },
  },
  methods: {
    // –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Ä–µ–≥–∏—Å—Ç—Ä–∞
    capitalizeFirst(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },

    // –º–µ—Ç–æ–¥ –∏–∑–º–µ–Ω–µ–Ω–∏—è –ø–æ–ª—è, –µ—Å–ª–∏ —ç—Ç–æ –∫–æ–Ω—Ç–∞–∫—Ç, —Ç–æ –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –ø–µ—Ä–µ—Ö–æ–¥ –Ω–∞ –µ–≥–æ —Å—Ç—Ä–∞–Ω–∏—Ü—É, –µ—Å–ª–∏ –ø–æ–ª–µ —Å –¥–∞–Ω–Ω—ã–º–∏, —Ç–æ –ø–æ—è–≤–ª—è–µ—Ç—Å—è input –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è
    editBtn() {
      if (this.type === "contacts") {
        return this.$emit("open-contact", this.item);
      } else {
        this.isEditing = true;
      }
    },

    // –º–µ—Ç–æ–¥ –ø—Ä–∏–Ω—è—Ç–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏–π –¥–ª—è –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π
    accept–°hanges() {
      if (this.value.trim()) {
        this.isEditing = false;
        this.item.value = this.value;
        this.value = "";
      }
    },

    // –æ—Ç–∫—Ä—ã—Ç–∏–µ –º–æ–¥–µ–ª–∏ –¥–ª—è –ø—Ä–∏–Ω—è—Ç–∏—è ............................................................... —Å–¥–µ–ª–∞—Ç—å —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—ã–º
    showModal() {
      this.$emit("show-modal", {
        id: this.item.id,
        title: "–û—Ç–º–µ–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è?",
        name: this.item.name,
        type: "change",
        callBack: () => this.stopChanging(),
      });
    },

    // –ø—Ä–µ–∫—Ä–∞—â–µ–Ω–∏–µ –∏–∑–º–µ–Ω–µ–Ω–∏–π
    stopChanging() {
      this.isEditing = false;
      this.value = "";
    },
  },
};
</script>

<style scoped>
input {
  height: 2rem;
}
button {
  cursor: pointer;
  width: 2rem;
  height: 2rem;
  margin: auto 0;
  border: 0px;
  border-radius: 1rem;
  background-color: tomato;
  color: white;
}
.change_field {
  margin-top: 0.5rem;
  margin-left: 3rem;
  height: 2.4rem;
  display: flex;
  align-content: center;
  justify-content: center;
}
.edit-btn {
  border-radius: 0;
  width: 2rem;
  height: 2.4rem;
}
</style>
