<template>
  <form @submit.prevent="addNewField" class="guide">
    <span>
      <!-- –≤—ã–ø–ª—ã–≤–∞—é—â–∏–π —Å–ø–∏—Å–æ–∫ —Ç–∏–ø–æ–≤ –ø–æ–ª–µ–π, –≤–∏–¥–µ–Ω —Ç–æ–ª—å–∫–æ –∫–æ–≥–¥–∞ –æ—Ç–∫—Ä—ã—Ç –∫–æ–Ω—Ç–∞–∫—Ç -->
      <select v-if="type === 'contact'" v-model="name">
        <option value="" disabled selected>–í—ã–±–µ—Ä–∏—Ç–µ –∑–Ω–∞—á–µ–Ω–∏–µ –∏–∑ —Å–ø–∏—Å–∫–∞</option>
        <option
          aria-placeholder="dsdsd"
          v-for="field in fields"
          :key="field.index"
          :value="field"
        >
          {{ capitalizeFirst(field) }}
        </option>
      </select>

      <!-- –≤–≤–æ–¥–Ω–æ–π –ø–æ–ª–µ –¥–ª—è –∏–º–µ–Ω–∏, –≤–∏–¥–µ–Ω —Ç–æ–ª—å–∫–æ –∫–æ–≥–¥–∞ –æ—Ç–∫—Ä—ã—Ç –∫–æ–Ω—Ç–∞–∫—Ç—ã -->
      <input
        v-else
        type="text"
        maxlength="30"
        minlength="3"
        pattern="^[A-Za-z–ê-–Ø–∞-—è–Å—ë]+$"
        placeholder="Enter name..."
        required
        v-model="name"
      />

      <!-- –ø–æ–ª–µ –¥–ª—è –≤–≤–æ–¥–∞ –∑–Ω–∞—á–µ–Ω–∏—è -->
      <input
        type="text"
        maxlength="30"
        minlength="1"
        placeholder="Enter value..."
        required
        v-model="value"
      />
      <button type="submit">ADD</button>
    </span>
  </form>
</template>

<script type="text/x-template" id="anchored-heading-template">
export default {
  name: "guide",
  data() {
    return {
      name: "",
      value: "",
      fields: ["name",'number','email','age', 'status','other'],
    };
  },
  props: {
    item: {
      type: Array,
    },
    type: {
      type: String,
      required: true
    },
  },
  methods: {
    // –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Ä–µ–≥–∏—Å—Ç—Ä–∞
    capitalizeFirst(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },

    // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–≥–æ –ø–æ–ª—è, –ø–æ–¥—Ö–æ–¥–∏—Ç –¥–ª—è –ª—é–±–æ–≥–æ –ª–∏—Å—Ç–∞
    addNewField() {
      if (this.name.trim() && this.value.trim()) {
        const field = {
          id: Date.now(),
          name: this.name,
          value: this.value,
        };
        this.$emit("add", field);
        this.name = "";
        this.value = "";
      }
    },
  },
};
</script>

<style scoped>
form {
  margin: 2rem;
}
button {
  font-weight: bold;
  background-color: tomato;
  color: white;
}
span {
  margin: auto;
  width: 40rem;
  display: grid;
  grid-template-columns: 45% 45% 10%;
  border: 2px solid rgb(255, 255, 255);
}
input {
  border: 0px;
  padding: 0.5rem;
}
</style>
